import * as layout from "../../routes/$layout.js";
export { layout };
export { default as ErrorComponent } from "../components/error.js";

export const components = [
	() => import("../../routes/index.js"),
	() => import("../../routes/kontakt.js"),
	() => import("../../routes/about.js"),
	() => import("../../routes/boty/index.js"),
	() => import("../../routes/boty/[slug].js")
];

export const routes = (d => [
	{
		// index.svelte
		pattern: /^\/$/,
		parts: [
			{ i: 0 }
		]
	},

	{
		// kontakt.svelte
		pattern: /^\/kontakt\/?$/,
		parts: [
			{ i: 1 }
		]
	},

	{
		// about.svelte
		pattern: /^\/about\/?$/,
		parts: [
			{ i: 2 }
		]
	},

	{
		// boty/index.svelte
		pattern: /^\/boty\/?$/,
		parts: [
			{ i: 3 }
		]
	},

	{
		// boty/[slug].svelte
		pattern: /^\/boty\/([^/]+?)\/?$/,
		parts: [
			null,
			{ i: 4, params: match => ({ slug: d(match[1]) }) }
		]
	}
])(decodeURIComponent);